.container {
  margin: 50px 0;
}

.card {
  margin: 0 2% !important;
}

.infected {
  border-bottom: 10px solid rgb(65, 105, 225);
}

.recovered {
  border-bottom: 10px solid rgb(127, 255, 0);
}

.deaths {
  border-bottom: 10px solid rgb(220, 20, 60);
}

@media (max-width: 770px) {
  .card {
    margin: 2% 0 !important;
  }
}
